
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
body{line-height:1.6;font-family:-apple-system-font,"Helvetica Neue",sans-serif
}
*{margin:0;padding:0
}
a img{border:0
}
a{text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0)
}
body{background-color:#fbf9fe
}
.router-view{width:100%;-webkit-animation-duration:.5s;animation-duration:.5s;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-backface-visibility:hidden;backface-visibility:hidden
}
.vux-pop-out-enter-active,.vux-pop-out-leave-active,.vux-pop-in-enter-active,.vux-pop-in-leave-active{will-change:transform;height:100%;position:absolute;left:0
}
.vux-pop-out-enter-active{-webkit-animation-name:popInLeft;animation-name:popInLeft
}
.vux-pop-out-leave-active{-webkit-animation-name:popOutRight;animation-name:popOutRight
}
.vux-pop-in-enter-active{-webkit-perspective:1000;perspective:1000;-webkit-animation-name:popInRight;animation-name:popInRight
}
.vux-pop-in-leave-active{-webkit-animation-name:popOutLeft;animation-name:popOutLeft
}
@-webkit-keyframes popInLeft{
from{opacity:0;-webkit-transform:translate3d(-100%, 0, 0);transform:translate3d(-100%, 0, 0)
}
to{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
}
@keyframes popInLeft{
from{opacity:0;-webkit-transform:translate3d(-100%, 0, 0);transform:translate3d(-100%, 0, 0)
}
to{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
}
@-webkit-keyframes popOutLeft{
from{opacity:1
}
to{opacity:0;-webkit-transform:translate3d(-100%, 0, 0);transform:translate3d(-100%, 0, 0)
}
}
@keyframes popOutLeft{
from{opacity:1
}
to{opacity:0;-webkit-transform:translate3d(-100%, 0, 0);transform:translate3d(-100%, 0, 0)
}
}
@-webkit-keyframes popInRight{
from{opacity:0;-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)
}
to{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
}
@keyframes popInRight{
from{opacity:0;-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)
}
to{opacity:1;-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)
}
}
@-webkit-keyframes popOutRight{
from{opacity:1
}
to{opacity:0;-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)
}
}
@keyframes popOutRight{
from{opacity:1
}
to{opacity:0;-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0)
}
}


